.user-menu {
    min-width: 150px;
    min-height: 100px;
    cursor: context-menu;

    img {
        object-fit: cover;
    }
}

.user-profile {
    border-radius: 10px;
    width: 100%;
    height: 100%;
    padding: 0 10px;
}

.user-page-link {
    border-top: 1px solid var(--background-dark-blue);
    padding: 5px 0;
    font-size: 18px;
    margin-top: 10px;
    text-align: center;
}

.follow-inf {
    text-align: left;
    margin-left: 10px;
    
    h6 {
        font-size: 18px;
    }

    i, span {
        color: var(--text-light-gray);
        font-size: 16px;
    }
}